.whywe {
	padding: 40px 0 85px;
	overflow: hidden; }
.quality {
	margin: 80px -3% 0;
	font-size: 0;
	text-align: center;
	@include md {
		margin-top: 50px; }
	&__item {
		padding: 0 3%;
		width: 25%;
		@include f('ll', 24);
		vertical-align: top;
		display: inline-block;
		@include md {
			padding: 0 3%;
			font-size: 20px;
			line-height: (24/20); }
		@include sm {
			margin-top: 65px;
			padding: 0 8.5%;
			width: 50%; }
		&:first-child,
		&:nth-child(2) {
			@include sm {
				margin-top: 0; } } }
	&__picture {
		height: 108px;
		line-height: 108px; }
	&__img {
		vertical-align: middle;
		display: inline-block;
		img {
			display: block; } }
	&__text {
		margin-top: 30px;
		strong {
			@include f('black'); } } }

.steps {
	padding: 155px 0 0;
	@include md {
		padding: 120px 0 0; }
	&__topic {
		max-width: 460px;
		@include f('lt', 24);
		@include md {
			max-width: 335px;
			font-size: 18px;
			line-height: (30/18); } }
	.btn {
		margin-top: 30px; }
	&__title-lg {
		margin-bottom: 8px;
		@include md {
			margin-bottom: 4px; } }
	&__wrap {
		position: relative;
		padding-top: 430px;
		@include md {
			padding-top: 332px;
			font-size: 0;
			text-align: center; } }
	&__list {
		list-style-type: none;
		counter-reset: list; }
	&__item {
		position: absolute;
		padding-left: 98px;
		@include f('lli', 15, 24);
		@include md {
			position: static;
			margin-top: 40px;
			padding-left: 35px;
			width: 33%;
			vertical-align: top;
			text-align: left;
			display: inline-block; }
		&:first-child,
		&:nth-child(2) {
			@include md {
				margin-top: 0; } }
		&:nth-child(odd) {
			@include md {
				margin-left: 10%; } }
		&:nth-child(even) {
			@include md {
				margin-left: 10%; } }
		&:nth-child(-n+9) {
			.steps__title {
				&:before {
					@include md {
						content: "0" (counter(list) "."); } } } }
		&_first {
			top: 243px;
			left: 37px; }
		&_second {
			top: 143px;
			left: 222px; }
		&_third {
			top: 155px;
			left: 667px; }
		&_fourth {
			top: 30px;
			left: 847px;
			min-width: 318px;
			@include md {
				min-width: 0; } } }
	&__logo {
		position: absolute;
		top: 266px;
		left: 455px;
		padding-left: 0;
		text-align: center;
		&:before {
			display: none; }
		@include md {
			position: absolute;
			top: 142px;
			left: 408px; } }
	&__numb {
		position: absolute;
		top: 0;
		left: 0;
		padding: 7px 15px 7px 20px;
		color: $black;
		@include f('black', 36);
		line-height: 1;
		text-transform: uppercase;
		background-color: $lightBlue;
		@include md {
			padding: 5px 7px 4px 12px;
			font-size: 23px; }
		&:before {
			position: absolute;
			content: "";
			top: 100%;
			left: 0;
			@include arr(6,24,$lightBlue, tl);
			@include md {
				@include arr(4,16,$lightBlue, tl); } }
		&:after {
			position: absolute;
			content: "";
			top: 0;
			left: 100%;
			@include arr(12,50,$lightBlue, tl);
			@include md {
				@include arr(8,32,$lightBlue, tl); } }
		.steps__item_first & {
			@include md {
				top: 180px;
				left: 147px; } }
		.steps__item_second & {
			@include md {
				top: 117px;
				left: 259px; } }
		.steps__item_third & {
			@include md {
				top: 128px;
				left: 537px; } }
		.steps__item_fourth & {
			@include md {
				top: 49px;
				left: 641px; } } }
	&__title {
		padding-top: 2px;
		@include f('ll', 18, 24);
		@include md {
			position: relative; }
		&_lg {
			opacity: 0.4;
			color: $white;
			@include f('black', 18, 24);
			text-transform: uppercase;
			@include md {
				font-size: 13px;
				line-height: (18/13); } }
		&:before {
			@include md {
				position: absolute;
				top: 0;
				left: -40px;
				color: $yellow;
				@include f('black', 20);
				counter-increment: list;
				content: (counter(list) "."); } }
		.steps__logo & {
			&:before {
				display: none; } } }
	&__link {
		margin-top: 12px;
		line-height: 20px;
		transition: border-color .2s ease-in-out;
		border-bottom: 1px solid rgba($white, 0.5);
		display: inline-block;
		@include hover {
			border-color: transparent; } }
	&__img {
		margin: 0 auto 12px;
		width: 93px;
		@include md {
			width: 70px; }
		img {
			width: 100%;
			display: block; } } }

